import React, { forwardRef } from 'react';
import Tooltip from '@material-ui/core/Tooltip';
import { makeStyles } from '@material-ui/core/styles';
import { muiPalette } from './muiPalette';

const useStyles = makeStyles(theme => ({
  tooltip: {
    margin: theme.spacing(1)
  }
}));

const ValueLabelComponent = ({ children, open, value }) => {
  const classes = useStyles();
  return (
    <Tooltip
      open={open}
      classes={classes}
      enterTouchDelay={0}
      placement="top"
      title={value}>
      {children}
    </Tooltip>
  );
};

const ThumbComponent = forwardRef((props, ref) => {
  return (
    <span
      {...props}
      ref={ref}
      data-qa-id={`thumb-handle-${props['data-index']}`}
    />
  );
});

const SIZES = {
  small: 272,
  medium: 352,
  large: 492
};

const DEFAULT_SIZE = SIZES.medium;

export const sliderOverride = {
  props: {
    MuiSlider: {
      ValueLabelComponent,
      ThumbComponent
    }
  },
  theme: {
    MuiSlider: {
      root: {
        width: props => SIZES[props.size] || DEFAULT_SIZE,
        padding: '13px 4px'
      },
      track: {
        backgroundColor: muiPalette.palette.primary.main,
        height: 4,
        borderRadius: 12
      },
      rail: {
        height: 4,
        opacity: 1,
        backgroundColor: muiPalette.palette.grey[300],
        borderRadius: 12
      },
      thumb: {
        backgroundColor: muiPalette.palette.primary.main,
        width: 14,
        height: 14,
        '&:hover': {
          boxShadow: `0px 0px 0px 4px ${muiPalette.palette.action.hover}`
        },
        '&:focus-visible': {
          boxShadow: `0px 0px 0px 4px ${muiPalette.palette.action.focus}`
        }
      },
      mark: {
        height: 4,
        width: 4,
        backgroundColor: muiPalette.palette.grey[400],
        borderRadius: 4
      },
      markActive: {
        color: muiPalette.palette.text.primary,
        backgroundColor: 'transparent'
      },
      markLabel: {
        color: muiPalette.palette.text.secondary,
        transform: 'translateX(-35%)'
      }
    }
  }
};
